import { ComponentProps } from 'preact';
import { useCallback, useEffect, useRef, useState } from 'preact/hooks';

import { CurrentKeyDetail } from '../UNSAFE_Collection';

import { useCollectionFocusRing } from '../hooks/PRIVATE_useCollectionFocusRing';
import { mergeProps } from '../utils/UNSAFE_mergeProps';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { classNames } from '../utils/UNSAFE_classNames';

import { Menu } from './Menu';
import { MenuContext } from './MenuContext';
import { useMenuCurrentKey } from './useMenuCurrentKey';
import {
  getPrevNextKey,
  getFirstVisibleKey,
  keyExtractor,
  getLastVisibleKey,
  getKeyboardCloseProps,
  getMenuPointerDown
} from './menuUtils';

type MenuProps = ComponentProps<typeof Menu>;

type MenuContainerProps = Omit<MenuProps, 'isOpen' | 'anchorRef'> & {
  display: 'sheet' | 'dropdown';
};

const styles = {
  menuContainer: cssProps`${{
    outlineStyle: 'none'
  }}`,
  dropdownMenuContainer: cssProps`${{
    paddingTop: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-spacing-2x)',
    paddingBottom: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-spacing-2x)'
  }}`,
  sheetMenuContainer: cssProps`${{
    paddingTop: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-spacing-5x)',
    paddingBottom: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-spacing-2x)'
  }}`
};

export const MenuContainer = ({
  children,
  onClose,
  accessibleLabel,
  initialFocus = 'menu',
  display
}: MenuContainerProps) => {
  const menuContainerRef = useRef<HTMLDivElement | null>(null);
  const initialFocusRef = useRef(initialFocus);

  const [currentKey, setCurrentKey] = useState<string | undefined>();

  const [possibleFocus, setPossibleFocus] = useState<'pending' | 'menu' | 'firstItem'>('pending');

  const changeKey = useCallback((detail: CurrentKeyDetail<string>) => {
    setCurrentKey(detail.value);
    setPossibleFocus('firstItem');
  }, []);

  const [showFocusRing, focusRingProps] = useCollectionFocusRing(
    (elem: Element | null) => {
      return !!menuContainerRef.current?.contains(elem);
    },
    ['ArrowUp', 'ArrowDown'],
    (elem: Element) => {
      const nextKey = keyExtractor(elem as HTMLElement);
      return !!nextKey;
    }
  );

  const { currentKeyProps } = useMenuCurrentKey(
    (element) => keyExtractor(element),
    getPrevNextKey(menuContainerRef.current, true, currentKey),
    getPrevNextKey(menuContainerRef.current, false, currentKey),
    () => getFirstVisibleKey(menuContainerRef.current),
    () => getLastVisibleKey(menuContainerRef.current),
    currentKey,
    changeKey
  );

  useEffect(() => {
    const menuContainer = menuContainerRef.current;

    if (menuContainer) {
      if (initialFocusRef.current === 'menu') {
        // At this point floating has visibility set as hidden, so also menuContainer. Settimeout added so menuContainer
        // is visible at that point and can be focused
        setTimeout(() => {
          menuContainer.focus();
        }, 0);
        setPossibleFocus('menu');
      } else {
        const firstItemKey = getFirstVisibleKey(menuContainerRef.current);
        if (firstItemKey) {
          changeKey({ value: firstItemKey });
        } else {
          setPossibleFocus('menu');
        }
      }
    }
  }, [initialFocusRef, changeKey]);

  // TODO - Reuse a dropdown component instead of creating floating by ourselves.
  return (
    <div
      ref={menuContainerRef}
      tabIndex={possibleFocus === 'firstItem' ? -1 : 0}
      role="menu"
      aria-label={accessibleLabel}
      class={classNames([styles.menuContainer, styles[`${display}MenuContainer`]])}
      {...mergeProps(
        focusRingProps,
        currentKeyProps,
        getMenuPointerDown(),
        getKeyboardCloseProps(onClose)
      )}>
      <MenuContext.Provider
        value={{
          onClose,
          currentKey,
          showFocusRing
        }}>
        {children}
      </MenuContext.Provider>
    </div>
  );
};
