import { ComponentProps } from 'preact';
import { forwardRef } from 'preact/compat';
import { useCallback, useEffect } from 'preact/hooks';

import { cssProps } from '@oracle/oraclejet-cssprops';
import { useMenuSwipe } from './useMenuSwipe';
import useModal from '../UNSAFE_Modal/hooks/useModal';
import { HTMLAttributesSignalExcluded } from '../utils/UNSAFE_attributeUtils';

import { Menu } from './Menu';
import { MenuContainer } from './MenuContainer';

type IntrinsicProps = Pick<HTMLAttributesSignalExcluded<HTMLDivElement>, 'children'>;

const styles = {
  modalWrapper: cssProps`${{
    position: 'fixed',
    top: 0,
    bottom: 0,
    left: 0,
    right: 0,
    zIndex: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-z-index-popup)'
  }}`,
  modalContainer: cssProps`${{
    position: 'absolute',
    top: 0,
    bottom: 0,
    left: 0,
    right: 0
  }}`,
  sheetMenuContainerWrapper: cssProps`${{
    borderRadius: '16px 16px 0 0',
    backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-bg-color-content)',
    position: 'absolute',
    bottom: 0,
    left: 0,
    right: 0,
    maxHeight: 'calc(100% - 96px)',
    overflowY: 'auto',
    overscrollBehaviorY: 'contain',
    touchAction: 'pan-y',
    userSelect: 'none'
  }}`,
  modalBackdrop: cssProps`${{
    position: 'absolute',
    top: '0',
    right: '0',
    bottom: '0',
    left: '0',
    backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-scrim-color)'
  }}`
};

const ModalWrapper = ({ children }: IntrinsicProps) => (
  <div class={styles.modalWrapper}>{children}</div>
);
const ModalContainer = forwardRef<HTMLDivElement, IntrinsicProps>(({ children }, ref) => (
  <div class={styles.modalContainer} ref={ref}>
    {children}
  </div>
));
//ModalBackdrop on modal folder not setting the style on oj-c enviroment
//Creating it here so we don't see that issue
const ModalBackdrop = () => <div class={styles.modalBackdrop} />;

export const SheetMenu = ({
  onClose,
  ...menuProps
}: Omit<ComponentProps<typeof Menu>, 'anchorRef'>) => {
  useEffect(() => {
    // We set the overflow body as hidden when mounted and revert to original overflow value when is
    // unmounted. This is done to don't allow backgound being scrolled
    // This will be handled by modal/popup when ready
    const initialOverflow = document.body.style.overflow;
    document.body.style.overflow = 'hidden';
    return () => {
      document.body.style.overflow = initialOverflow;
    };
  }, []);

  const dismissHandler = useCallback(() => {
    onClose?.({ reason: 'dismissed' });
  }, [onClose]);

  const { modalRef } = useModal({ isOpen: true, onBackdropClick: dismissHandler });
  //TODO: Use useSwipe when it works on components that could scroll.
  const { swipeProps } = useMenuSwipe(dismissHandler);

  // TODO: For now we are mimicking current modal but using a "different position setting" (using absolute positioning instead of flex).
  // Have to use modal/popup component directly instead doing this composition when that component could satisfy our requirments.
  return (
    <ModalWrapper>
      <ModalBackdrop></ModalBackdrop>
      <ModalContainer ref={modalRef}>
        <div class={styles.sheetMenuContainerWrapper} {...swipeProps}>
          <MenuContainer {...{ ...menuProps, onClose }} display="sheet"></MenuContainer>
        </div>
      </ModalContainer>
    </ModalWrapper>
  );
};
