import { ComponentProps, useCallback, useRef } from 'preact/compat';

import { cssProps } from '@oracle/oraclejet-cssprops';
import { Floating } from '../UNSAFE_Floating';

import { MenuContainer } from './MenuContainer';
import { Menu } from './Menu';
import { useOutsideClick } from '../hooks/UNSAFE_useOutsideClick';

const styles = {
  // TODO: Handle vertical and horizontal overflow. Get rid of maxWidth on a next drop so horizontal and also vertical flow is handled correctly.
  // It's hard to relay just in css, we might need to relay on some kind of observer. Maybe should be handled on Floating/Dropdown component.
  base: cssProps`${{
    maxWidth: '90vw',
    width: 'max-content',
    borderRadius: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-border-radius-lg)',
    boxShadow: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-dropdown-box-shadow)',
    backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-bg-color-content)'
  }}`
};

export const DropdownMenu = ({
  anchorRef,
  onClose,
  ...dropdownMenuProps
}: ComponentProps<typeof Menu>) => {
  const floatingRef = useRef<HTMLElement | null>(null);

  const useOutsideClickHandler = useCallback(
    (e: MouseEvent) => {
      onClose?.({ reason: 'outsideClick', target: e.target as Element });
    },
    [onClose]
  );

  useOutsideClick({
    ref: [floatingRef],
    handler: useOutsideClickHandler
  });

  return (
    <Floating
      class={styles.base}
      ref={floatingRef}
      anchorRef={anchorRef}
      placement="bottom-start"
      offsetValue={{
        mainAxis: 4, //TODO: stable does not have a 4px gap, so we'll need to figure out how to make this themable.
        crossAxis: 0
      }}>
      <div
        style={{
          minWidth: `${
            anchorRef.current && anchorRef.current.offsetWidth > MENU_MIN_WIDTH
              ? anchorRef.current.offsetWidth
              : MENU_MIN_WIDTH
          }px`
        }}>
        <MenuContainer {...{ onClose, ...dropdownMenuProps }} display="dropdown" />
      </div>
    </Floating>
  );
};

const MENU_MIN_WIDTH = 200;
